@import url("https://fonts.googleapis.com/css?family=Inconsolata:400,700")
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;300;400;500;600;700;800&display=swap')

$bg-color: hsla(200, 25, 15, 100%)
$bash-color-orange: orange
$bash-color-pink: pink
$bash-color-green: green
$bash-bg-color-orange: orange

.tsparticles-canvas-el
  position: absolute
  width: 100%
  top: 0px
  z-index: -1

body
  background: #150d2be8
  font-family: "JetBrains Mono"
  .grid
    margin-top: 150px
    display: grid
    grid-template-columns: 3fr 8fr 3fr
    
  .module
    box-sizing: border-box
    grid-column: 2
    border-radius: 4px
    overflow: hidden
    box-shadow: 0px 0px 20px 10px #0000003b
    .module-header
      width: 100%
      overflow: auto
      padding: 5px 8px
      background: #130f2a
      .box-label
        float: left
        
        .file-name
          font-size: 15px
          line-height: 15px
          color: lighten(#535c68, 20)
          
          
      .box-button
        display: flex
        float: right
        margin-right: 17px
        opacity: 0.7
        .button
          background: #ff7979
          width: 13px
          height: 13px
          border-radius: 100%
          margin: 5px 2px
          cursor: pointer
          
          &.red
            background: #ff7979
          &.orange
            background: #ffbe76
          &.green
            background: #badc58
    .module-body
      position: relative
      background: #130f2a
      height: 400px
      overflow-y: auto
      
      &:hover
        background: #130f2aa3 !important
      .text-label
        position: absolute
        bottom: 8px
        right: 8px
        padding: 2px 8px
        border-radius: 2px
        background:#171717eb
        color: rgba(251, 205, 164, 0.6)
        font-weight: bold
        display: none
        

       
      .text-area
        padding: 10px
        &::before
          content: "Skandal-Dev@Skdev:$ "
          color: #6eff6ee8
          font-weight: bold
          position: relative
          padding: 0px
          margin: 0
        code
          color: white
          padding: 0px
          font-weight: bold
          margin: 0 0px
          font-size: 14px
        .output
          opacity: 0.8
          color: #fad407
          background: #fae00608
          &::before
            content: "> "
            position: relative
            margin-left: 0px
            opacity: 0.8
          


ul
  margin: 0
  padding: 0
  margin-left: 30px
  margin-top: -20px
  list-style: japanese-formal
  color: white
  li
    &::before
      color: white

  .fetch
    color: #ff9ef9
    font-weight: 900
    background-color: #f25e956b
    padding: 0 5px
    
