@import url(https://fonts.googleapis.com/css?family=Inconsolata:400,700);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;300;400;500;600;700;800&display=swap);
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.tsparticles-canvas-el{position:absolute;width:100%;top:0px;z-index:-1}body{background:#150d2be8;font-family:"JetBrains Mono"}body .grid{margin-top:150px;display:grid;grid-template-columns:3fr 8fr 3fr}body .module{box-sizing:border-box;grid-column:2;border-radius:4px;overflow:hidden;box-shadow:0px 0px 20px 10px #0000003b}body .module .module-header{width:100%;overflow:auto;padding:5px 8px;background:#130f2a}body .module .module-header .box-label{float:left}body .module .module-header .box-label .file-name{font-size:15px;line-height:15px;color:#848f9d}body .module .module-header .box-button{display:flex;float:right;margin-right:17px;opacity:.7}body .module .module-header .box-button .button{background:#ff7979;width:13px;height:13px;border-radius:100%;margin:5px 2px;cursor:pointer}body .module .module-header .box-button .button.red{background:#ff7979}body .module .module-header .box-button .button.orange{background:#ffbe76}body .module .module-header .box-button .button.green{background:#badc58}body .module .module-body{position:relative;background:#130f2a;height:400px;overflow-y:auto}body .module .module-body:hover{background:#130f2aa3 !important}body .module .module-body .text-label{position:absolute;bottom:8px;right:8px;padding:2px 8px;border-radius:2px;background:#171717eb;color:rgba(251,205,164,.6);font-weight:bold;display:none}body .module .module-body .text-area{padding:10px}body .module .module-body .text-area::before{content:"Skandal-Dev@Skdev:$ ";color:#6eff6ee8;font-weight:bold;position:relative;padding:0px;margin:0}body .module .module-body .text-area code{color:#fff;padding:0px;font-weight:bold;margin:0 0px;font-size:14px}body .module .module-body .text-area .output{opacity:.8;color:#fad407;background:#fae00608}body .module .module-body .text-area .output::before{content:"> ";position:relative;margin-left:0px;opacity:.8}ul{margin:0;padding:0;margin-left:30px;margin-top:-20px;list-style:japanese-formal;color:#fff}ul li::before{color:#fff}ul .fetch{color:#ff9ef9;font-weight:900;background-color:#f25e956b;padding:0 5px}
